import React from 'react';
import PropTypes from 'prop-types';
import ClearFilters from '../filters/finder__clear__filter';

/**
 * A list of currently applied filters as buttons that can be clicked to
 * remove them.
 *
 * @param {object} props
 * @return {object} - React component.
 */
function finder__appliedfilters(props) {
    const removeFacet = facet => {
        const newQuery = props.query;
        delete newQuery.facets[facet];
        facet === 'G' && props.query.facets.rSub
            ? delete newQuery.facets.rSub
            : null;
        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const facetKeys = Object.keys(props.query.facets);

    if (facetKeys.length > 0) {
        return (
            <React.Fragment>
                <div className="finder__applied__filters__heading">
                    <h2>Applied filters</h2>
                    <ClearFilters clear={props.clear} query={props.query} />
                </div>
                <div className="finder__appliedfilters__wrap">
                    <ul className="finder__appliedfilters">
                        {facetKeys.map(facet => {
                            const labels = props.config.facetLabels.filter(
                                    facetLabel => facetLabel.meta === facet
                                )[0],
                                facetValue = props.query.facets[facet];

                            return (
                                <li key={facet}>
                                    <button
                                        type="button"
                                        onClick={() => removeFacet(facet)}
                                    >
                                        <span>
                                            <span
                                                className="fal fa-times icon"
                                                aria-label="Remove filter"
                                            />{' '}
                                            <span>
                                                {labels.name}:&nbsp;
                                                {labels.meta === 'rSub'
                                                    ? facetValue.replace(
                                                          props.query.facets.G +
                                                              '_',
                                                          ''
                                                      )
                                                    : facetValue}
                                            </span>
                                        </span>
                                    </button>
                                </li>
                            );
                        })}
                    </ul>
                </div>
            </React.Fragment>
        );
    } else {
        return null;
    }
}

finder__appliedfilters.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
};

export default finder__appliedfilters;
